@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/*imports */

/* main */

body {
  font-family: 'Roboto', cursive;
  font-weight: 100;
}


.bg {
  background-image: url(/think/static/media/decorative-circle-half.514155ac.png);
  background-repeat: no-repeat;
  background-size:80%;
  background-position: 250% 0%;
  background-attachment:fixed;

  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main_wrapper {
  flex-direction: row;
}
.txt_1{
  font-size:30px;/*better to use relative*/
  color: #3f4a75;
  width:300px;
  text-align: left;
  
}
.txt_2{
  font-size:30px;
  color: #db487e;
}
.list {
  font-size:20px;
  color: #3f4a75;
  padding-top: 30px !important;
}
.listItem {
  padding-left: 0 !important;
  padding-bottom: 0 !important;
}
.list_selected {
  font-weight: 500;

}
.pic_1_1 {
  width: 600px;
  height: 600px;
  padding:50px;
} 
@media only screen and ( min-width: 768px)
{
  .pic_one {
    background-image: url(/think/static/media/64152862_mask.9200d98e.png);
    
  }
  .pic_two {
    background-image: url(/think/static/media/64152862_mask_2.f3b2eb73.png);
  }
  .pic_three {
    background-image: url(/think/static/media/64152862_mask_3.c0f95892.png);
  }
  .pic_four {
    background-image: url(/think/static/media/64152862_mask_4.8dbfd70c.png);
  }
  .pic_five {
    background-image: url(/think/static/media/64152862_mask_5.ad25f03e.png);
  }
  .row_1 {
    height: 250px;
  }
  .row_2 {
    height: 588px;
    padding-top:34px;
  }
  .row_3 {
    height: 100%;
  }
}
.pic_one,
.pic_two,
.pic_three,
.pic_four,
.pic_five {
  width: 100%;
  height: 100%;
  background-size: cover;
  border-radius: 50%;

  display: flex;
  flex-direction: column;
}
.inner_row{
  width:100%;
  
}
.row_1 {
  display:flex;
  justify-content: center;
  margin-top:-4px;
}
.row_2 {
  display: flex;
  justify-content: space-between; 
  margin-left: 44px;
  
}
.row_3 {
  display: flex;
  align-items:flex-end;
  justify-content: space-around; 
}
.btn {
  width: 100px;
  height:100px;
}
.btn_top {
  display: flex;
  justify-content: center;
}
.btn_lh {
  display: flex;
  justify-content: flex-start;
  margin-left: -20px;
}
.btn_rh {
  display: flex;
  justify-content: flex-start;
  margin-right: 25px;
}
.btn_btm_lh {
  display:flex;
  justify-content: flex-start;
  padding-right: 10px;
}
.btn_btm_rh {
  display:flex;
  justify-content: flex-end;
}
.fab {
  border:2px solid #3f4a75 !important;
}
.selected {
  background: #3f4a75 !important;
  color: white !important;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

